/* ---------------------------------------------------------contact style----------------------------------------------------------------------- */
.contact_section {
  padding-top: 20px;
  margin-top: 80px;
  overflow: hidden;
}

.contact_section .title::after {
  content: "get in touch";
}
.contact_section .title {
  position: relative;
  text-align: center;
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 60px;
  padding-bottom: 20px;
  font-family: "Ubuntu", sans-serif;
}
.contact_section .title::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 50%;
  width: 180px;
  height: 3px;
  background: #111;
  transform: translateX(-50%);
}
.contact_section .title::after {
  position: absolute;
  bottom: -8px;
  left: 50%;
  font-size: 20px;
  color: crimson;
  padding: 0 5px;
  background: #fff;
  transform: translateX(-50%);
}

.contact_container {
  /* height: 100vh; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #eee */
  margin-bottom: 50px;
}

.contact_card {
  height: 500px;
  width: 800px;
  background-color: #fff;
  position: relative;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  position: relative;
  font-family: "Poppins", sans-serif;
}

.form {
  width: 100%;
  height: 100%;
  display: flex;
}

.left-side {
  width: 30%;
  background-color: #fff;
  height: 100%;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.left-side .line1 {
  height: 350px;
  width: 100%;
  background-color: #4b66ef;
  position: absolute;
  clip-path: polygon(0 0, 100% 0, 100% 50%, 50% 100%, 50% 100%, 0% 50%);
  z-index: 10;
}

.left-side .line2 {
  height: 350px;
  width: 440px;
  background-color: #31c8ff;
  position: absolute;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
  top: 150px;
  z-index: 9;
}

.left-side .line3 {
  height: 330px;
  width: 100%;
  background-color: #f0aec5;
  position: absolute;
  clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  z-index: 11;
  bottom: 0;
}

.contact-us {
  display: flex;
  justify-content: center;
}

.contact-us p {
  z-index: 11;
  writing-mode: vertical-lr;
  text-orientation: mixed;
  transform: rotate(-180deg);
  color: #fff;
  font-size: 25px;
  font-weight: 700;
  position: absolute;
  top: 20px;
  left: 30px;
}

.contact-us span {
  position: absolute;
  top: 180px;
  left: 49px;
  height: 80px;
  width: 2px;
  background-color: #fff;
  z-index: 11;
}

.left-side .social {
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 55px;
}

.social i {
  margin-right: 20px;
  z-index: 12;
  font-size: 23px;
  color: #fff;
  transition: all 0.6s;
}

.social i:nth-child(1) {
  position: absolute;
  bottom: -90px;
  left: 0;
}

.social i:nth-child(2) {
  position: absolute;
  bottom: -90px;
  left: 30px;
  transition-delay: 0.3s;
}

.social i:nth-child(3) {
  position: absolute;
  bottom: -90px;
  left: 70px;
  transition-delay: 0.6s;
}

.social i:nth-child(4) {
  position: absolute;
  bottom: -90px;
  left: 110px;
  transition-delay: 0.9s;
}

.left-side:hover .social i {
  bottom: 0;
}

.right-side {
  width: 70%;
  background-color: #fff;
  height: 100%;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}

.top-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

.top-div h2 {
  font-weight: 800;
  font-size: 25px;
}

.top-div p {
  font-size: 13px;
  font-weight: 800;
}

.stats {
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
  margin-top: 30px;
}

.stats1 i {
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #111;
  transition: all 0.5s;
  transform: scale(0);
}
.stats2 i {
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #111;
  transition: all 0.5s;
  transform: scale(0);
}
.stats3 i {
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #111;
  transition: all 0.5s;
  transform: scale(0);
}

.right-side:hover .stats1 i {
  transform: scale(1.3);
}
.right-side:hover .stats2 i {
  transform: scale(1.3);
}
.right-side:hover .stats3 i {
  transform: scale(1.3);
}

.stats1 span {
  font-size: 12px;
  font-weight: 600;
}
.stats2 span {
  font-size: 12px;
  font-weight: 600;
}
.stats3 span {
  font-size: 12px;
  font-weight: 600;
}

.card-content {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.card-content span {
  height: 230px;
  width: 300px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 2px 2px 20px 5px #c8d2ff;
  padding: 25px;
  box-sizing: border-box;
}

input[type="text"] {
  height: 15px;
  width: 100%;
  border: none;
  border-bottom: 2px solid #c8d2ff;
  outline: 0;
  caret-color: #c8d2ff;
  color: #c8d2ff;
  margin-bottom: 20px;
}

.input-text {
  position: relative;
}

.input-text small {
  position: absolute;
  top: 2px;
  left: 0;
  color: #c8d2ff;
  font-weight: 600;
  transition: all 0.5s;
}

.input-text input:focus ~ small,
.input-text input:valid ~ small {
  top: -15px;
  font-size: 12px;
  color: black;
}

.textarea {
  position: relative;
  margin-top: 8px;
  color: #c8d2ff;
}

.textarea textarea {
  height: 60px;
  width: 100%;
  resize: none;
  border: none;
  outline: none;
  border-bottom: 2px solid #c8d2ff;
  caret-color: #c8d2ff;
  color: #c8d2ff;
}

.textarea small {
  position: absolute;
  top: -2px;
  left: 0;
  transition: all 0.5s;
  font-weight: 600;
}

.textarea textarea:focus ~ small,
.textarea textarea:valid ~ small {
  top: -20px;
  font-size: 12px;
  color: black;
}

.buttons {
  display: flex;
  justify-content: center;
  position: relative;
}

.buttons button {
  height: 40px;
  width: 150px;
  position: absolute;
  bottom: -140px;
  color: white;
  background-color: #4b6fef;
  border-radius: 8px;
  transition: all 0.5s;
  cursor: pointer;
  border: none;
}

.buttons button:hover {
  background-color: blue;
}

.right-side:hover button {
  position: absolute;
  bottom: -20px;
}

/* ----------------------------------------------media query------------------------------------------------ */
@media only screen and (max-width: 600px) {
  .contact_section {
    padding-top: 20px;
    margin-top: 80px;
    overflow: hidden;
  }
  .contact_section .title::after {
    position: absolute;
    bottom: -8px;
    left: 50%;
    font-size: 14px;
    color: crimson;
    padding: 0 5px;
    background: #fff;
    transform: translateX(-50%);
  }
  .stats2 span {
    display: none;
  }
  .stats2 i {
    display: none;
  }
  .stats {
    display: flex;
    justify-content: space-around;
    padding: 0 40px;
    margin-top: 30px;
  }
  .stats3 span {
    font-size: 12px;
    font-weight: 600;
    margin-left: 2rem;
  }
}

@media only screen and (min-width: 600px) {
  .about_section {
    margin-bottom: 50px;
    height: auto;
    padding-top: 95px;
  }
}

@media only screen and (min-width: 768px) {
}

@media only screen and (min-width: 992px) {
  .about_section {
    margin-bottom: 50px;
    height: auto;
    padding-top: 95px;
  }
}
